* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: #2a2624;
}

:root{
  --index: calc(1vw + 1vh);
  --color-header: #f4efec;
  --color-text: #cdc6c3;
  --gallery-gap: calc(var(--index) * 10);
}

